.containerPro{
    flex-flow: row wrap;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    padding: 25px 0px 0px 0px;
  }
  .cardCon{
      width: 18rem;
      height: auto;
      background-color: $white;
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      margin: 2rem 1.5rem;
      border-radius: 1rem;
  }
  .cardWraper{
    text-align: center;
    background: $secondaryColor;
    padding: 50px 0px 45px 0px;
      
  }
  .cardTitelpro{
      padding: 1.5rem 0;
      font-size: 3rem;
      color: $white;
      text-shadow: 0 0 5px $black;
  }
  
  .cardImg{
      width: 100%;
      margin: 1rem;
  }
  .cardTitel{
      margin-top: .7rem;
  }
  .cardText{
      margin-bottom: .7rem;
      text-align: center;
  }
  .cardBtn{
      padding: .5rem 1rem;
      background-color: $secondaryColor;
      border-radius: 1rem;
      margin: 1rem 0;
      text-transform: uppercase;
      color: $primaryColor;
      font-size: 1.5rem;
      text-decoration: none;
      border: none;
  }
  .cardBtn:hover{
      transform: translateY(-3px);
      box-shadow: .5rem .3rem 2rem $hoverBtn;
  }
  .cardBtn:active{
      transform: translateY(0);
      box-shadow: none;
  }
  .cartList {
    list-style-type: none;
    margin: 0px 20px;

}
.cartList li {
    display: flex;
    justify-content: space-around;
    margin-top: 10px;
}
.cartNamnPro{
  margin-right: 10px;
  max-width: 30px;

}
.cartInner{
  display: flex;
  flex-direction: row;
  justify-content: center;
  min-width: 40px;
}

.cartInner input{
    width: 40px;
    border: none;
    text-align: center;
    box-shadow: .5rem .3rem 2rem $black;
}

.decrease{
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: $white;
    width: 20px;
    height: 25px;
    border: none;
    border-top-left-radius: 4px;
    border-bottom-left-radius: 4px;
    box-shadow: .5rem .3rem 2rem $black
}

.increase{
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: $white;
    width: 20px;
    height: 25px;
    border: none;
    border-top-right-radius: 4px;
    border-bottom-right-radius: 4px;
    box-shadow: .5rem .3rem 2rem $black;
}
.cartpriss{
  max-width: 30px;
}
.row {
  text-align: center;
  display: flex;
  flex-flow: column;
  align-items: center;
  padding: 25px 0px 25px 0px;
  }