@import "./variabels";
@import "./navbar";
@import "./burger";
@import "./modal";
@import "./front";
@import "./cards";
@import "./about";
@import "./footer";


*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Raleway', sans-serif;
}
nav {
    display: flex;
    justify-content: space-around;
    /* flex-direction: row; */
    align-items: center;
    background: $secondaryColor;
    padding: 10px;
    min-height: 9vh;
}
    
    @media screen and (max-width:900px){
            body{
            overflow-x: hidden;
        }
    
        .logo{
            position: absolute;
            left: 40px;
            font-size: 20px;
        }
    
        .nav-link{
            position: absolute;
            right: 0;
            width: 100vw;
            top: 8vh;
            height: 50vh;
            display: flex;
            flex-direction: column;
            align-items: center;
            background-color: $secondaryColor;
            border: 1px solid $darkSecColor;
            transform: translateX(100%);
            transition: transform 0.5s ease-in;
            z-index: 1;
        
            #nav-kund{
                opacity: 1;
            }
        }
    
        .burger {
            position: absolute;
            right: 8%;
            display: block;
        }
            .nav-active {
                transform: translateX(0%);
            }
            
            @keyframes navLinkFade {
                from{
                    opacity: 0;
                    transform: translateX(1000px);
                }
                to{
                    opacity: 1;
                    transform: translateX(0px);
                }
            }
            
            .toggle .line1 {
                transform: rotate(-45deg) translate(-5px,4px);
            }
            
            .toggle .line2 {
                opacity: 0;
            }
            
            .toggle .line3 {
                transform: rotate(45deg) translate(-5px,-4px);
            }
        }
        
        .modal{
            position: relative;
            right: 0;
            top: -1rem;
            width: 100vw;
            height: 50vh;
            display: flex;
            flex-direction: column;
            align-items: center;
        
            .modal___text{
                position: absolute;
                top: 10px;
                padding: 10px;
                width: 80%;
                display: flex;
                justify-content: space-between;
                flex-direction: row;
            }
    
            .modal___split {
                position: relative;
                left: 1%;
                top: 3rem;
                width: 90%;
                z-index: 2;
                color: $grey;
                border: 0.5px solid;
            }
    
            .modal___kop{
                position: relative;
                right: 1rem;
                top: 85%;    
            }
        }
    

